<template>
  <div id="app">
    <Header></Header>
    <div class="dMain">

      <router-view></router-view>

    </div>
    <Footer></Footer>

  </div>
</template>

<script>
  import Footer from '@/components/common/Footer'
  import Header from '@/components/common/Header'
  export default {
    name: 'App',
    components: {
      Header,
      Footer
    },
    data() {
      return {

      }
    },
    created() {
      this.openNotify();
    },
    methods: {
      openNotify: function() {
        this.$notify({
          title: '望周知',
          message: '网站备案号，浙网安备号皆为占位符。',
          offset: 100,
          position: 'bottom-left'
        })
      }
    }
  }
</script>

<style>
  html,
  body {
    /* background-color: #598998; */
  }

  * {
    margin: 0;
    padding: 0;
    background-repeat: no-repeat;
  }

  input,
  button {
    display: block;
    border: none;
    outline: none;
  }

  span {
    font-size: 14px;
  }



  li {
    list-style: none;
  }

  a {
    text-decoration: none;
    color: #333;
    font-size: 14px;
  }

  .display-flex {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
  }

  img {
    display: block;
  }

  .d-button {
    width: 60px;
    height: 34px;
    background-color: rgba(0, 0, 0, 1);
    color: #fff;
    text-align: center;
    line-height: 34px;
    border-radius: 5px;
    cursor: pointer;
  }

  .d-button:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }

  .dMain {
    width: 1200px;
    margin: 0 auto;
    padding-top: 40px;
  }

  .d-fourBlock {
    width: 100%;
    height: 40px;
  }

  .d-twoBlock {
    width: 100%;
    height: 20px;
  }

  .d-oneBlock {
    width: 100%;
    height: 10px;
  }

  .d-title {
    font-size: 18px;
    font-weight: bold;
    border-left: 4px solid #000;
    padding-left: 10px;
    margin-bottom: 20px;
  }

  .d-module-title {
    font-size: 16px;
    padding-bottom: 10px;
    border-bottom: 2px solid #fff;
    margin-bottom: 10px;
  }


  .d-sTitle {
    font-size: 18px;
    font-weight: bold;
    color: #409EFF;
    padding: 60px 0 20px 0;
  }

  .controller-title {
    font-size: 14px;
    padding-bottom: 20px;
  }
</style>



