import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [{
      path: '*',
      name: 'NotFound',
      component: resolve => (require(['@/components/NotFound'], resolve))
    },
    {
      path: '/',
      name: 'Home',
      component: resolve => (require(['@/components/Home'], resolve))
    },
    {
      path: '/login',
      name: 'Login',
      component: resolve => (require(['@/components/Login'], resolve))
    },
    {
      path: '/register',
      name: 'Register',
      component: resolve => (require(['@/components/Register'], resolve))
    },
    {
      path: '/article',
      name: 'Article',
      component: resolve => (require(['@/components/Article'], resolve))
    },
    {
      path: '/about',
      name: 'About',
      component: resolve => (require(['@/components/About'], resolve))
    },
    {
      path: '/blogroll',
      name: 'Blogroll',
      component: resolve => (require(['@/components/Blogroll'], resolve))
    },
    {
      path: '/write',
      name: 'Write',
      component: resolve => (require(['@/components/Write'], resolve))
    },
    {
      path: '/details',
      name: 'Details',
      component: resolve => (require(['@/components/Details'], resolve))
    },
    {
      path: '/course',
      name: 'Course',
      component: resolve => (require(['@/components/Course'], resolve))
    },
    {
      path: '/tags',
      name: '/Tags',
      component: resolve => (require(['@/components/Tags'], resolve))
    },
    {
      path: '/message',
      name: 'Message',
      component: resolve => (require(['@/components/Message'], resolve))
    },
    {
      path: '/controller',
      name: 'Controller',
      component: resolve => (require(['@/components/Controller'], resolve)),
      redirect: '/controller/personal',
      children: [{
          path: 'personal',
          name: 'Personal',
          component: resolve => (require(['@/components/controller/Personal'], resolve))
        },
        {
          path: 'articlemanager',
          name: 'ArticleManager',
          component: resolve => (require(['@/components/controller/ArticleManager'], resolve))
        },
        {
          path: 'usermanager',
          name: 'UserManager',
          component: resolve => (require(['@/components/controller/UserManager'], resolve))
        }
      ]
    },
    {
      path: '/uploadimg',
      name: 'UploadImg',
      component: resolve => (require(['@/components/experiment/UploadImg'], resolve))
    }
  ]
})


export default router;



