<template>
  <div class="header">
    <div class="main">
      <div class="content display-flex">
        <div class="left">
          <ul class="display-flex">
            <li>
              <i class="el-icon-s-home"></i>
              <router-link to="/">首页</router-link>
            </li>
            <li>
              <i class="el-icon-s-management"></i>
              <router-link to="/tags">标签</router-link>
            </li>
            <!-- <li>
              <i class="el-icon-s-order"></i>
              <router-link to="/article">文章</router-link>
            </li> -->
            <li>
              <i class="el-icon-s-promotion"></i>
              <router-link to="/blogroll">友链</router-link>
            </li>
            <li>
              <i class="el-icon-video-camera-solid"></i>
              <router-link to="/course">时间线</router-link>
            </li>
            <li>
              <i class="el-icon-s-custom"></i>
              <router-link to="/about">关于我</router-link>
            </li>
            <!-- <li>
              <i class="el-icon-s-custom"></i>
              <router-link to="/uploadimg">文件上传</router-link>
            </li> -->
          </ul>
        </div>

        <!-- 搜索框 -->
        <div class="middle">
          <el-input placeholder="请输入标题,支持模糊匹配" v-model.trim="article_title" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="searchArticle">搜索</el-button>
          </el-input>
        </div>


        <div class="right">
          <div v-if="$store.state.login_exist">
            <div class="custom-info">
              <div class="custom-name">
                <i class="el-icon-user-solid"></i>
                {{$store.state.login_name}}
              </div>
              <div class="custom-personal">
                <div>
                  <i class="el-icon-s-open"></i>
                  <router-link to="/write">写博客</router-link>
                </div>
                <div>
                  <i class="el-icon-phone-outline"></i>
                  <router-link to="/message">吐槽</router-link>
                </div>
                <div>
                  <i class="el-icon-s-tools"></i>
                  <router-link to="/controller">控制台</router-link>
                </div>
                <!-- <div>
                  <i class="el-icon-s-tools"></i>
                  <router-link to="/login">登录注册</router-link>
                </div> -->
                <div>
                  <i class="el-icon-no-smoking"></i>
                  <span class="d-span" @click="loginOut">退出</span>
                </div>
              </div>
            </div>
          </div>
          <div v-else="$store.state.login_exist" class="d-button" @click="goToLogin">登录</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        url: '/article',
        article_title: '',
      }
    },
    methods: {
      goToLogin: function() {
        this.$router.push("/login").catch(err => {});
      },
      loginOut: function() {
        this.$store.commit('loginOutChangeName');
        this.$router.push('/login');
      },
      // 搜索文章 根据标题, 支持模糊匹配
      searchArticle: function() {
        if (this.article_title != '') {
          this.$router.push({
            name: 'Article',
            params: {
              article_title: this.article_title
            }
          }).catch(err => {})

          this.getArticle();


        } else {
          this.$message({
            message: '搜索内容不要为空',
            type: 'warning'
          })
        }

      },
      // 获取匹配的文章列表
      getArticle: function() {
        if (this.article_title != '') {
          this.$axios.get(this.url + '/queryArticleByTitle/' + this.article_title).then(res => {
            if (res.data.code == 200) {
              this.$store.state.article_group = res.data.body;
            } else {

              this.$store.state.article_group = [];

              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }
          })
        }
      }
    }
  }
</script>

<style scoped>
  div {
    font-size: 14px;
  }

  .header {
    width: 100%;
    min-width: 1200px;
    box-sizing: border-box;
    padding: 0 20px;
    background-color: #f9f9f9;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  }

  .content {
    width: 100%;
    justify-content: space-between;
  }

  .left li {
    padding: 20px;
    margin-right: 20px;
  }

  .custom-info {
    position: relative;
    min-width: 100px;
    cursor: default;
    padding: 20px 0;
  }

  .custom-personal {
    width: 100px;
    position: absolute;
    display: none;
    top: 50px;
    background-color: #f9f9f9;
    z-index: 2;
  }

  .custom-personal>div {
    padding: 5px;
  }

  .custom-personal>div:hover {
    background-color: #C0C4CC;
  }

  .custom-info:hover .custom-personal {
    display: block;
  }

  .right .d-button {
    margin-top: 10px;
  }

  .middle {
    padding-top: 10px;
  }
</style>



