<template>
  <div class="footer">
    <div class="main">
      <div class="content">
        &copy; 2018-2020 半个程序员 - XXX 版权所有
        <br>
        总访问量 <span>{{pv_count}}</span> 次 访客量 <span>{{uv_count}}</span> 人次
        <br>
        <a href="https://beian.miit.gov.cn/" target="_blank">备案号 123456789号</a>
        <br>
        <a href="javascript:;" target="_blank">浙网安备 123456789987654号</a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Footer',
    data() {
      return {
        url: '/puv',
        pv_count: 0, // 总访问量
        uv_count: 0 // 访客量
      }
    },
    created() {
      this.getPV();
      this.addPV();

      this.getUV();
      this.addUV();
    },
    methods: {
      // 获取访问量
      getPV: function() {
        this.$axios.get(this.url + '/getPV').then(res => {
          if (res.data.code == 200) {
            this.pv_count = res.data.body.pv;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 访问量 +1
      addPV: function() {
        this.$axios.get(this.url + '/pvAddOne').then(res => {})
      },
      // 获取访客量
      getUV: function() {
        this.$axios.get(this.url + '/queryUVCount').then(res => {
          if (res.data.code == 200) {
            this.uv_count = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 访客量 +1
      addUV: function() {
        this.$axios.get(this.url + '/uvAddOne').then(res => {})
      }
    }
  }
</script>

<style scoped>
  div{
    font-size: 14px;
  }
  
  .footer {
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
    border-top: 2px solid #f9f9f9;
    margin-top: 40px;
  }

  .content {
    text-align: center;
    color: #909399;
    font-family: "楷体";
    line-height: 20px;
  }

  .content span {
    font-weight: bold;
    color: #303133;
  }

  .content a {
    color: #909399;
  }

  .content a:hover {
    text-decoration: underline;
  }
</style>



