import Vue from 'vue'
import Vuex from 'vuex'

//挂载Vuex
Vue.use(Vuex)

//创建VueX对象
const store = new Vuex.Store({
  state: {
    login_name: '', // 用户名
    login_exist: false,
    login_phone: '',
    token: '',
    email_reg: /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/, // 邮箱正则
    phone_reg: /^1[3456789]\d{9}$/, // 手机号正则
    article_group: [], // 文章标题返回的搜索列表
    firstIndex: 0 // 首页列表分页created执行的第一条索引
  },
  mutations: {
    loginChangeName: function(state, options) {
      this.state.login_name = options.username;
      this.state.login_phone = options.phone;
      this.state.login_exist = true;
    },
    loginOutChangeName: function(state) {
      this.state.login_name = '';
      this.state.login_phone = '';
      this.state.login_exist = false;
    },
    // 设置 token
    setToken: function(state, token) {
      this.state.token = token;
      window.localStorage.setItem('token', token);
    }
  }
})

export default store;



